import { useFormik } from 'formik'
import React, { useState } from 'react'
import CitizenApplyApiList from './CitizenApplyApiList';
import axios from 'axios';
import ApiHeader from './ApiList/ApiHeader';
import * as yup from 'yup'
import './Fonts.css'
import { TiMessages } from 'react-icons/ti'
import { MdSend } from 'react-icons/md'


function CitizenMessageScreenModified(props) {

    const { api_safindependentComment } = CitizenApplyApiList()

    const [sendMsg, setsendMsg] = useState()

    console.log("props..applicationIdfun", props.applicationIdfun)
    console.log("props..workflowIdfun", props.workflowIdfun)

    let applicationId = props.applicationId
    let workflowId = props.workflowId

    const validationSchema = yup.object({
        message: yup.string().required('Enter message')
    })

    const initialValues = {
        message: '',
    }


    const formik = useFormik({
        initialValues: initialValues,

        onSubmit: (values) => {
            handleSendComment(values)
        }
        ,
        validationSchema

    });



    const handleSendComment = (values) => {
        console.log("post data...", values);

        let requestBody = {
            // "workflowId": workflowId,
            "applicationId": applicationId,
            "comment": values.message
        }
        console.log('before citizen comment...', requestBody)
        axios.post(api_safindependentComment,
            requestBody,
            ApiHeader()
        )
            .then(function (response) {
                // setloader(false)
                console.log('send msg.....', response)
                props?.notify('Message successfully sent !')
                props?.closeModalFun()


            })
            .catch(err => {
                console.log("err", err)
                props?.notify('Something went wrong !')
                props?.closeModalFun()
            })
    }

    const handleClose = () => {
        props.fun(false)
    }
    return (
        <>

            <div className='p-4 md:w-[25vw] w-full'>
                <div className='flex'>
                    <div className='flex-1'>
                        <h1 className="text-gray-900 text-xl font-semibold title-font mb-4 flex items-center poppins">
                            <p className=' font-sans flex items-center'> <TiMessages fontSize={20} /> &nbsp; Send Message</p>
                        </h1>
                    </div>
                    <div className='flex-1 '>
                        <button className='float-right hover:scale-105 z-10 cursor-pointer' onClick={() => props?.closeModalFun()}>
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" class="w-8 h-8 text-red-500">
                                <path fill-rule="evenodd" d="M12 2.25c-5.385 0-9.75 4.365-9.75 9.75s4.365 9.75 9.75 9.75 9.75-4.365 9.75-9.75S17.385 2.25 12 2.25zm-1.72 6.97a.75.75 0 10-1.06 1.06L10.94 12l-1.72 1.72a.75.75 0 101.06 1.06L12 13.06l1.72 1.72a.75.75 0 101.06-1.06L13.06 12l1.72-1.72a.75.75 0 10-1.06-1.06L12 10.94l-1.72-1.72z" clip-rule="evenodd" />
                            </svg>
                        </button>
                    </div>
                </div>

                <form onSubmit={formik.handleSubmit}>

                    <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1'>
                        <textarea name='message' className='bg-white shadow-lg px-2 poppins rounded-md text-gray-700 border-2 border-gray-300' value={formik.values.message} onChange={formik.handleChange} />
                        <span className="text-red-600 absolute text-xs">{formik.touched.message && formik.errors.message ? formik.errors.message : null}</span>
                        <div className="flex mt-2">

                            <div className='flex-1 mx-auto mt-5'>
                                <button type='submit' className='bg-indigo-500 hover:bg-indigo-600 text-white poppins transition-all duration-200 ease-in-out rounded-sm px-6 py-[0.2rem] flex items-center'>Send &nbsp;<MdSend /></button>
                            </div>

                        </div>
                    </div>

                </form>
            </div>

        </>
    )
}

export default CitizenMessageScreenModified